const uni = require('uni').default;






















import pageHead from '../../../components/page-head.vue'

export default {
	data() {
		return {
			title: 'popup',
			showState: {
				top: false,
				middle: false,
				bottom: false
			},
			showMask: false,
			activePop: 'middle'
		}
	},
	components: {
		pageHead
	},
	methods: {
		show(evt) {
			var pos = evt.target.dataset.position
			switch (pos) {
				case 'top':
					this.activePop = 'top'
					break
				case 'bottom':
					this.activePop = 'bottom'
					break
				default:
					this.activePop = 'middle'
			}
			this.showMask = true
			this.showState[this.activePop] = true
		},
		hide() {
			this.showMask = false
			this.showState[this.activePop] = false
		}
	}
}



